<!-- by 添少 -->
<template>
  <section>
    <topbar>
      <template slot="before">
        <b class="iconfont icon-back" @click="$router.go(-1)"></b>
      </template>
      俱乐部详情
    </topbar>
    <section class="preview">
      <div class="cover">
        <img class="db fit-cover" :src="club.cover"/>
      </div>
      <div class="text">
        <h3>{{club.title}}</h3>
        <p>{{club.summary}}</p>
      </div>
      <div class="ft">
        <template v-if="isJoin">
          <button class="ui-button disabled">已加入俱乐部</button>
        </template>
        <template v-else>
          <button class="ui-button primary" @click="handJoin">加入俱乐部</button>
        </template>
      </div>
    </section>
    <section class="detail">
      <tab :select.sync="tab.select" :data="tab.list" @change="handChangeTab"></tab>
      <template v-if="tab.select===0">
        <section class="tc1">
          <template v-for="i in 10">
            <div class="ui-thumb">
              <div class="cover">
                <img class="db fit-cover" :src="club.cover"/>
              </div>
              <div class="text">
                <h4 class="l-e l1">{{tab.select}}塞班岛{{i}}</h4>
                <p class="info">Sasa俱乐部<b>俱乐部</b></p>
                <p class="l-e l2">目的地简介，行程的亮点等等的摘要信息放到这里</p>
              </div>
            </div>
          </template>
        </section>
      </template>
      <template v-if="tab.select===1">
        <section class="tc1">
          <template v-for="i in 10">
            <div class="ui-thumb">
              <div class="cover">
                <img class="db fit-cover" :src="club.cover"/>
              </div>
              <div class="text">
                <h4 class="l-e l1">{{tab.select}}塞班岛{{i}}</h4>
                <p class="info">Sasa俱乐部<b>俱乐部</b></p>
                <p class="l-e l2">目的地简介，行程的亮点等等的摘要信息放到这里</p>
              </div>
            </div>
          </template>
        </section>
      </template>
      <template v-if="tab.select===2">
        <section class="tc1">
          <template v-for="i in 10">
            <div class="ui-thumb">
              <div class="cover">
                <img class="db fit-cover" :src="club.cover"/>
              </div>
              <div class="text">
                <h4 class="l-e l1">{{tab.select}}塞班岛{{i}}</h4>
                <p class="info">Sasa俱乐部<b>俱乐部</b></p>
                <p class="l-e l2">目的地简介，行程的亮点等等的摘要信息放到这里</p>
              </div>
            </div>
          </template>
        </section>
      </template>
      <template v-if="tab.select===3">
        <section class="tc1">
          <template v-for="i in 10">
            <div class="ui-thumb">
              <div class="cover">
                <img class="db fit-cover" :src="club.cover"/>
              </div>
              <div class="text">
                <h4 class="l-e l1">{{tab.select}}塞班岛{{i}}</h4>
                <p class="info">Sasa俱乐部<b>俱乐部</b></p>
                <p class="l-e l2">目的地简介，行程的亮点等等的摘要信息放到这里</p>
              </div>
            </div>
          </template>
        </section>
      </template>
    </section>
  </section>
</template>

<script>
  import { mapState } from 'vuex'
  import Topbar from '@/components/topbar'
  import Tab from '@/components/tab'

  export default {
    name: 'club',
    components: {
      Topbar,
      Tab
    },
    computed: {
      ...mapState({}),
      isJoin () {
        return true
      }
    },
    data () {
      return {
        club: {
          cover: 'data:image/jpeg;base64,',
          title: 'Sasa俱乐部',
          summary: 'Sasa俱乐部的简介，由Sasa成立，带领过无数次的潜水行程，给俱乐部成员带来了终生难忘的美好回忆。期待和你一起探索世界哇。'
        },
        tab: {
          select: 0,
          list: ['课程', '行程', '装备', '帖子']
        },
      }
    },
    methods: {
      handChangeTab (index) {
        console.log(index)
      }
    }
  }
</script>

<style lang="scss" scoped>
  .ui-thumb {
    font-size: 14px;
    display: flex;
    .cover {
      /*width: 130px;*/
      height: 100px;
      flex: 0 0 130px;
    }
    .text {
      text-align: justify;
      padding-left: 10px;
      h4 {
        font-size: 18px;
      }
      .info {
        color: #888;
        b {
          background-color: #d33c44;
          font-size: 11px;
          color: #fff;
          padding: 0 5px;
          margin-left: 0.5em;
          display: inline-block;
          border-radius: 5px;
        }
      }
      flex: 1;
    }
  }

  .ui-button {
    background-color: #fff;
    padding: 6px 10px;
    border-radius: 10px;
    border-width: 0;
    &.primary {
      background-color: #1797c6;
      color: #fff;
    }
    &.disabled {
      background-color: #ccc;
      color: #fff;
    }
  }

  .preview {
    background-color: #fff;
    color: #333;
    margin-bottom: 10px;
    .text {
      padding: 0 15px;
      margin-bottom: 10px;
    }
    .cover {
    }

    h3 {
      font-size: 22px;
      font-weight: 600;
      padding-top: 5px;
    }
    .ft {
      text-align: center;
      padding-bottom: 15px;
    }
  }

  .detail {
    background-color: #fff;
    .tc1 {
      overflow: hidden;
      padding: 15px 15px 0;
      .ui-thumb {
        margin-bottom: 15px;
      }
    }
  }
</style>



